@keyframes contact_main{
    0%{
        transform: translate(0%);
    }
    50%{
        transform: translateX(-100%);
    }
    50.1%{
        transform: translateX(100%);
    }
    100%{
        transform :translateX(0%)
    }
}

@keyframes contact_clone{
    0%{
        transform: translateX(0%);
    }
    100%{
        transform: translateX(-200%);
    }
}

@keyframes circle {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.bg_img{
    position: relative;
    margin-top:200px;
    height: 900px;
    overflow: hidden;
}

.bg_img img{
    filter: brightness(95%);
}

.contact_slide_container{
    position: absolute;
    top:50%;
    left:0%;
    transform:translateY(-50%);
    display:flex;
    white-space: nowrap;
    font-size: 80px;
    color:#fffdf3;
}

.slide_item_one{
    animation: contact_main 60s infinite linear;
}

.slide_item_two{
    animation: contact_clone 60s infinite linear;
}


/*contact title*/
.contact{
    position: relative;
    height: 800px;
    background-color: #404646;
}

.contact_title{
    position: absolute;
    top:5%;
    left:2%;
    display:flex;
    align-items: center;
}
.contact_title div{
    width:40px;
    height: 40px;
    margin-right:18px;
    border-radius: 50%;
    background-color: #c5c940;
}

.contact_title h3{
    font-size:17px;
    font-weight: lighter;
    color: #fffdf3;
}

/*circle*/
.test{
  transform: rotate();  
}

.circle_email{
    position: absolute;
    top:-14%;
    right:3%;
    border-radius: 50%;
    background-color: #fffdf3;
    animation: circle 20s infinite linear;
}

/*email_icon*/
.email_icon{
    position: absolute;
    top:-3%;
    right: 7%;
    display:flex;
    flex-direction: column;
    align-items: center;
}

.email_icon a{
    fill: #404646;
    color:#404646;
}

.email_icon a:first-child{
    font-size: 30px;
}

.email_icon a:last-child{
    font-size: 13px;
}

/*contact_text*/
.contact_text{
    position: absolute;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    width:80%;
    margin: auto;
    color:#fffdf3;
}

.contact_text h3{
    font-size:24px;
    font-weight: lighter;
    font-family: 'Noto Sans KR', sans-serif;
}

.contact_text h1{
    font-size:130px;
    margin-bottom:50px;
}

.contact_text_email{
    display:flex;
    font-family: 'Noto Sans KR', sans-serif;
    width:100%;
}

.contact_text_email div:first-child{
    width:40%;
    margin-right:30px;
}

.contact_text_email div:last-child{
    width:40%;
    margin-left:30px;
}
.contact_text_email p{
    font-size:20px;
}
