/*총 컨테이너*/
.blog{
    position: relative;
    display:flex;
    height: 950px;
    margin-top:100px;
    overflow: hidden;
}

/*title*/
.blog_title{
    position: absolute;
    top:5%;
    left:2%;
    display:flex;
    align-items: center;
}
.blog_title div{
    width:40px;
    height: 40px;
    margin-right:18px;
    border-radius: 50%;
    background-color: #c5c940;
}

.blog_title h3{
    font-size:17px;
    font-weight: lighter;
    color: #fffdf3;
}

/*블로그 소개 글*/
.bolg_text{
    display:flex;
    flex-direction: column;
    justify-content: center;
    width:40%;
    padding-left:100px;
    border-right: 1px solid #878686;
    background-color: #404646;
    font-family: 'Cormorant Infant';
}

.bolg_text h4{
    font-size: 30px;
    margin:0px 0px 10px 0px;
    color:#fffdf3;
}

.bolg_text h3{
    font-size:50px;
    color:#c5c940;
    margin-bottom:25px;
}

.bolg_text p{
    font-size:20px;
    font-weight: bold;
    font-family: 'Noto Sans KR', sans-serif;
    margin:3px 0px 3px 0px;
    color: #fffdf3;
}

.bolg_text a{
    text-decoration: none;
}

.bolg_text button{
    display:flex;
    align-items: center;
    font-size: 17px;
    font-weight: bold;
    font-family: 'Noto Sans KR', sans-serif;
    margin-top:30px;
    fill: #fffdf3;
    color:#c5c940;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.bolg_text button svg{
    margin-top:3px;
    margin-right:10px;
    padding:2px 5px 2px 5px;
    border-radius: 50%;
    background-color: #c5c940;
}

/*블로그 글*/
.blog_container{
    width:60%;
    font-family: 'Noto Sans KR', sans-serif;
    background-color: #404646;
    overflow-y: scroll;
}

.blog_container::-webkit-scrollbar {
    display: none;
  }

.blog_item{
    display:flex;
    margin:50px 0px 50px 0px;
    padding:0px 30px 30px 30px;
    border-bottom: 1px solid #878686;
}

.blog_item img{
    width:330px;
    height:191px;
    border-radius: 40px;
}

.bolg_item_text{
    margin-left: 40px;
    margin-right: 40px;
}

.bolg_item_text h2{
    color: #fffdf3;
    margin:10px 0px 10px 0px;
}
.bolg_item_text p{
    color: #878686;
    font-weight: bold;
    margin-top:5px;
}