@import url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@300&family=Noto+Sans+KR:wght@300&display=swap');
/*Myname_slide_animation*/
@keyframes slide_main{
    0%{
        transform: translate(0%);
    }
    50%{
        transform: translateX(-100%);
    }
    50.1%{
        transform: translateX(100%);
    }
    100%{
        transform :translateX(0%)
    }
}


@keyframes slide_clone{
    0%{
        transform: translateX(0%);
    }
    100%{
        transform: translateX(-200%);
    }
}

@keyframes img_up {
    0%{
        opacity: 0;
        transform: translateY(30px);
    }
    100%{
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes img_down {
    0%{
        opacity: 1;
        transform: translateY(0px);
    }
    100%{
        opacity: 0;
        transform: translateY(30px);
    }
}

@keyframes img_up_back {
    0%{
        opacity: 0;
        transform: translateY(30px);
    }
    100%{
        opacity: 0.1;
        transform: translateY(0%);
    }
}
body{
    background-color: #faf7ed;
    font-family: 'Cormorant Infant';
    overflow-x: hidden;
}
/*총 부모 컨테이너*/
.home_container {
    display:flex;
    justify-content: space-between;
    color:#434b50;
}

/*왼쪽 컨테이너*/
.home_container_left{
    position: relative;
    width:50%;
    height:100vh;
    border-right: 1px solid #1d1d1d;
    overflow:hidden;
}


/*왼쪽 상단 webpublisher text*/
.portfolio_text{
    position: absolute;
    top:5%;
    left:5%;
    cursor: pointer;
}

.portfolio_text h2{
    font-size: 17px;
}

.portfolio_text p{
    font-size: 14px;
}


/*Name, Create 컨테이너*/
.myname_text{
    position: absolute;
    top:50%;
    left:5%;
    transform: translateY(-50%);
}


/*시작 글 Hello my name is text*/
.myname_text h2{
    margin:10px 0px 10px 0px;
}


/*Name Slide Container*/
.myname_slide_container{
    display:flex;
}

.myname_slide_container div{
    padding:0px 20px 0px 20px;
}

.myname_slide_container h1{
    width:100vw;
    font-size:200px;
}

.myname_slide_text_one{
    animation: slide_main 70s infinite linear;
}

.myname_slide_text_two{
    animation: slide_clone 70s infinite linear;
}

/*Create Slide Container*/
.create_slide_container{
    display: flex;
}

.create_slide_container h3{
    padding:0px 5px 0px 5px;
    font-size:20px;
}

.create_slide_text_one{
    display:flex;
    animation: slide_main 35s infinite linear;
}

.create_slide_text_two{
    display:flex;
    animation: slide_clone 35s infinite linear;
}

/*about me Container*/
.aboutme_btn{
    position: absolute;
    top:85%;
    left:50%;
    transform: translateX(-50%);
    padding:25px 40px 25px 40px;
    border: 1px solid #434b50;
    border-radius: 50%;
    color:#434b50;
    background-color: transparent;
    font-size:12px;
    font-weight: bold;
    font-family: 'Cormorant Infant';
    cursor:pointer;
}


/*오른쪽 컨테이너*/
.home_container_right{
    position: relative;
    width:50%;
    height:100vh;
    overflow: hidden;
}

/*information text*/
.infor_text{
    position: absolute;
    top:-100%;
    left:19.5%;
    font-size:320px;
    color: #434b50;
    opacity: 0.1;
    transform: rotate(90deg);
}

.mation_text{
    position: absolute;
    top:200%;
    right:20.5%;
    font-size:250px;
    color: #434b50;
    opacity: 0.1;
    transform: rotate(270deg);
}

/*introduce_text*/
.introduce_text{
    position: absolute;
    top:15%;
    right:24%;
    width:479px;
    opacity: 0;
    font-family: 'Noto Sans KR', sans-serif;
}

.introduce_text h1{
    font-size:25px;
    margin:40px 0px 30px 0px;
}

.introduce_text p{
    font-size:20px;
    font-weight: bold;
    margin-bottom:30px;
}


/*information img*/
.info_img{
    position: absolute;
    top:8%;
    left:17.5%;
    width:65%;
    border-radius:100px;
    opacity: 0.1;
    filter: brightness(30%);
}

/*menu 컨테이너*/
.menu_container{
    position: absolute;
    top:5%;
    right:2%;
    text-align: right;
}

.menu_container li {
    padding:0px 0px 8px 0px;
    font-size: 14px;
    color: #434b50;
    cursor:pointer;
}

/*information text*/

.information_text{
    position: absolute;
    top:50%;
    left:50%;
    transform: translateX(-50%);
    font-size:60px;
}

/*aboutinfo btn*/
.aboutinfo_btn{
    position: absolute;
    top:85%;
    left:50%;
    transform: translateX(-50%);
    padding:25px 40px 25px 40px;
    border: 1px solid #434b50;
    border-radius: 50%;
    color:#434b50;
    background-color: transparent;
    font-size:12px;
    font-weight: bold;
    font-family: 'Cormorant Infant';
    cursor:pointer;
}

/*home으로 돌아가는 버튼*/

.turn_back_home{
    position: absolute;
    bottom: 7%;
    right: 5%;
    width:60px;
    height: 60px;
    font-size: 15px;
    font-weight: bold;
    color:#434b50;
    background-color: transparent;
    border: 1px solid #434b50;
    border-radius: 50%;
    opacity: 0;
    font-family: 'Cormorant Infant';
    cursor: pointer;
}

/*tech stack btn*/

.techstack_btn{
    position: absolute;
    bottom:7%;
    left:50%;
    transform: translateX(-50%) translateY(200px);
    padding:25px 40px 25px 40px;
    font-size: 15px;
    border: 1px solid #434b50;
    border-radius: 50%;
    color:#434b50;
    background-color: transparent;
    font-weight: bold;
    font-family: 'Cormorant Infant';
    cursor:pointer;
}

/*tech stack*/

.tech_stack{
    position: absolute;
    top:10%;
    left:50%;
    transform: translateX(-50%);
    display:none;
    flex-direction: column;
    justify-content: space-evenly;
    width:1000px;
    height:700px;
    border-radius: 30px;
    background-color: #faf7ed;
}

.techskill_item{
    display:flex;
    align-items: center;
    margin:0px 150px 0px 150px;
}

.techskill_item img{
    width:90px;
}

.techskill_item h1{
    width:80px;
    margin:0px 50px 0px 50px;
    font-size:20px;
}

.techskill_item p{
    width:450px;
    font-size: 13.8px;
    font-family: 'Noto Sans KR', sans-serif;
}