@import url('https://fonts.googleapis.com/css2?family=Cormorant+Infant:wght@300&family=Noto+Sans+KR:wght@300&display=swap');

/*header*/
.header_text{
    margin:20px 0px 0px 20px;
    color: #434b50;
}

.header_text > *{
    font-size:20px;
}

/*name*/
.name_container{
    margin-top:150px;
    width:430px;
    overflow: hidden;
    color: #434b50;
    border-bottom: 1px solid #434b50;
}

.name_container h2{
    font-size:20px;
    margin-left:20px;
}

.name_slide_container{
    display:flex;
   white-space: nowrap;
}

.name_slide_container h1{
    font-size: 150px;
}

.name_slide_item{
    animation:  slide_main 40s infinite linear;
}

.name_slide_clone{
    animation: slide_clone 40s infinite linear;
}

/*fixed layout*/
.fixed_layout{
    position: absolute;
    top:0;
    right:4%;
    text-align: right;
    margin-top:20px;
    color: #434b50;
}

.fixed_layout li{
    padding-bottom:5px;
}

/*info container*/
.info_container{
    position: relative;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top:30px;
    color: #434b50;
}

.my_img{
    width:340px;
    border-radius: 3%;
    opacity: 0.3;
    filter: brightness(30%);
}

.info_text{
    display:none;
    width:340px;
    font-family: 'Noto Sans KR', sans-serif;
}

.info_text h1{
    font-size:20px;
    margin:50px 0px 20px 0px;
}

.info_text p{
    font-size:16px;
    margin-bottom:20px;
}

.info_container button{
    width:120px;
    height:60px;
    border-radius: 50%;
    border: 1px solid #434b50;
    background-color: transparent;
    font-family: 'Cormorant Infant';
    font-size:15px;
    font-weight: bold;
    margin-top:20px;
    margin-bottom:20px;
    color:#434b50;
}

.info_container button:last-child{
    display:none;
    margin-left:40px;
}

/*tech stack*/
.techstack_container{
    display:none;
    flex-direction: column;
    width:340px;
    margin: auto;
    margin-top: 20px;
    border-radius: 1%;
    
    font-family: 'Noto Sans KR', sans-serif;
    background-color: #434b50;
}

.techstack_item{
    display:flex;
    align-items: center;
    margin:30px 0px 30px 0px;
}

.techstack_item img{
    width:50px;
    margin-left: 6px;
}

.techstack_item h1{
    margin:0px 12px 0px 12px;
    font-family: 'Cormorant Infant';
    color:#faf7ed;
}

.techstack_item p{
    font-size:12px;
    color:#faf7ed;
    margin-right: 6px;
}