.blog_res{
    background-color: #434b50;
}

.blog_res_header{
    display:flex;
    align-items: center;
    padding:20px 0px 0px 10px;
}

.blog_res_header div{
    width:30px;
    height:30px;
    border-radius: 50%;
    margin-right:10px;
    background-color: #c5c940;
}

.blog_res_header h2{
    font-size:16px;
    color:#faf7ed;
    font-weight: lighter;
}

.blog_res_text{
    width:80%;
    margin: auto;
    margin-top:50px;
    color: #faf7ed;
    font-family: 'Noto Sans KR', sans-serif;
}

.blog_res_text h4{
    font-size:32px;
    margin-bottom:20px;
    font-family: 'Cormorant Infant';
}

.blog_res_text p {
    font-size:15px;
    margin-bottom:5px;
}

.blog_res_text button{
    display:flex;
    align-items: center;
    border: none;
    background-color: transparent;
    margin-top:15px;
    font-size:15px;
    color:#c5c940;
}

.blog_res_text a{
    text-decoration: none;
}

.blog_res_text button svg{
    width:15px;
    height:15px;
    background-color: #c5c940;
    border-radius: 50%;
    margin-right:5px;
    fill:#faf7ed;
}

.blog_res_container{
    margin-top:70px;
}

.blog_res_item{
    width:80%;
    margin: auto;
    color:#faf7ed;
    padding-bottom:70px;
}

.blog_res_item h2{
    font-size:19px;
    border-bottom: 1px solid #faf7ed;
    padding-bottom: 5px;
    font-family: 'Noto Sans KR', sans-serif;
}

.blog_res_item p{
    text-align: right;
    padding-top: 5px;
    margin-bottom:5px;
}

.blog_res_item h3{
    font-size:13px;
    margin:10px 0px 10px 0px;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: lighter;
}

.blog_res_item img{
    width:100%;
    margin: auto;
}
