.web_res{
    background-color: #434b50;
}

.web_header{
    display:flex;
    align-items: center;
    padding:20px 0px 0px 10px;
    margin-bottom:50px;
}

.web_header div{
    width:30px;
    height:30px;
    border-radius: 50%;
    margin-right:10px;
    background-color: #c5c940;
}

.web_header h2{
    font-size:16px;
    color:#faf7ed;
    font-weight: lighter;
}

.web_item{
    width:80%;
    margin: auto;
    color:#faf7ed;
    padding-bottom:70px;
}

.web_item h2{
    font-size:32px;
    border-bottom: 1px solid #faf7ed;
    padding-bottom: 5px;
}

.web_item p{
    text-align: right;
    padding-top: 5px;
    margin-bottom:5px;
}

.web_item h3{
    font-size:13px;
    margin:10px 0px 10px 0px;
    font-family: 'Noto Sans KR', sans-serif;
}
.web_img_container img{
    width:100%;
    margin: auto;
}

.web_skill{
    display:flex;
    width:100%;
}

.web_skill h4{
    font-size:8px;
    margin:0px 10px 0px 0px;
    padding:4px 8px 4px 8px;
    border: 1px solid #faf7ed;
    border-radius: 30px;
}

.web_skill h4:last-child{
    margin-right: 0px;
}
