.nonfiction_publishing{
    background-color: #383b3b;
}

.nonfiction_container{
    width:100vw;
    height: 100vh;
    background-color: #383b3b;
}

/*슬라이드 텍스트*/
.slide_non_text {
    position: fixed;
    top:50%;
    transform: translateY(-50%);
    display:flex;
    font-size: 180px;
    color:#787878;
    filter: brightness(30%);
    opacity: 0.2;
    white-space: nowrap;;
}

/*중간 글씨 컨테이너*/
.nonfiction_text_container{
    position: absolute;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    display:flex;
    flex-direction: column;
    align-items: center;
    color:#fffdf3;
}

.nonfiction_text_container p{
    font-size : 13px;
}

.nonfiction_text_container h2{
    font-size : 90px;
    margin: 20px 0px 20px 0px;
}

.nonfiction_skill{
    display:flex;
}

.nonfiction_skill div{
    margin:0px 10px 0px 0px;
    padding:5px 12px 5px 12px;
    border: 1px solid #fffdf3;
    border-radius: 20px;
    font-size:10px;
    cursor: default;
}

/*visit 버튼*/
.visit{
    position: absolute;
    bottom:5%;
    left:50%;
    transform: translateX(-50%);
    font-size:40px;
    padding:5px 20px 5px 20px;
    border-radius: 15px;
    color:#fffdf3;
    background-color: #c5c940;
    cursor: pointer;
}

/*back 버튼*/
.back_homepage{
    position: fixed;
    bottom:-5%;
    left:0;
    font-size:180px;
    color:transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #fffdf3;
    cursor: pointer;
    z-index: 9999;
}

/*논픽션 설명 글*/
.nonfiction_explain{
    position: relative;
    display:flex;
    flex-direction: column;
    align-items: center;
    width:40%;
    margin: auto;
    border-radius: 20px;
    color:#404646;
    background-color: #fffdf3;
    font-family: 'Noto Sans KR', sans-serif;  
}

.nonfiction_explain h1{
    margin:50px 0px 30px 0px;
    font-size: 20px;
}

.nonfiction_explain p{
    margin:20px 0px 20px 0px;
    font-size:16px;
}

/*논픽션 홈페이지 웹사이트 버전*/
.nonfiction_web{
    position: relative;
    width:50%;
    margin:auto;
    margin-top: 200px;
    font-family: 'Noto Sans KR', sans-serif;  
}

.nonfiction_web h1{
    text-align: center;
    font-size: 15px;
    font-weight: bold;
    color:#fffdf3;
    margin-bottom:20px;
}

.nonfiction_web img{
    width:100%;
}

/*논픽션 모바일 웹사이트 버전*/

.nonfiction_mobile{
    position: relative;
    width:25%;
    margin:auto;
    margin-top: 100px;
    font-family: 'Noto Sans KR', sans-serif;  
}

.nonfiction_mobile h1{
    text-align: center;
    font-size: 15px;
    font-weight: bold;
    color:#fffdf3;
    margin-bottom:20px;
}