@keyframes web_up {
    0%{
        opacity: 0;
        transform: translateY(50px);
    }
    100%{
        opacity: 1;
        transform: translateY(0px);
    }
}

.web_container{
    position: relative;
    width: 96%;
    margin: auto;
    border-radius: 15px;
    background-color: #404646;
    z-index: 2;
}

/*title*/
.web_title{
    position: absolute;
    top:1%;
    left:2%;
    display:flex;
    align-items: center;
}
.web_title div{
    width:40px;
    height: 40px;
    margin-right:18px;
    border-radius: 50%;
    background-color: #c5c940;
}

.web_title h3{
    font-size:17px;
    font-weight: lighter;
    color: #fffdf3;
}

/*컨테이너*/
.web_container_row{
    display:flex;
    justify-content: space-evenly;
    padding-bottom: 100px;
    
}

.web_container_row_second{
    display:flex;
    justify-content: space-evenly;
    padding-bottom: 100px;
    opacity: 0;
}

/*img*/
.img_container{
    width:710px;
    height: 710px;
    overflow: hidden;
    border-radius: 15px;
    cursor: pointer;
}

.img_container_one{
    width:100%;
    height: 710px;
    overflow: hidden;
    border-radius: 15px;
    cursor: pointer;
}

/*skill*/
.skill{
    display:flex;
}

.skill h4{
    margin:0px 10px 0px 0px;
    padding:5px 12px 5px 12px;
    border: 1px solid #fffdf3;
    border-radius: 20px;
    font-size:10px;
    cursor: default;
}

/*nonfinction*/

.web_item_nonfinction{
    display:flex;
    flex-direction: column;
    margin-top:250px;
    color: #fffdf3;
    cursor: default;
}

.web_item_nonfinction img{
    width:710px;
}

.web_item_nonfinction h2{
    font-size:48px;
    border-bottom: 1px solid #fffdf3;
    padding-bottom: 10px;
}

.web_item_nonfinction p{
    text-align: right;
    font-size:11.5px;
    margin:5px 0px 20px 0px;
}

.web_item_nonfinction h3{
    margin:30px 0px 10px 0px;
    font-size: 14px;
    font-family: 'Noto Sans KR', sans-serif;
}


