/*사이드 publishing 텍스트*/

.side_text{
    position: absolute;
    top: 33%;
    right:-68%;
    transform: rotate(90deg);
    font-size:500px;
    color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #5d6061;
    z-index: -1;
}
