@keyframes circle {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.contact_res{
    position: relative;
    background-color: #434b50;
}

.contact_re_bg_img{
    position: relative;
}

.contact_re_bg_img img{
 height: 300px;
}

.contact_res_slide_container{
    display:flex;
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    white-space: nowrap;
    font-size: 80px;
    color:#fffdf3;
}

.contact_res_title{
    display:flex;
    align-items: center;
    padding:20px 0px 0px 10px;
    margin-bottom:50px;
}

.contact_res_title div{
    width:30px;
    height:30px;
    border-radius: 50%;
    margin-right:10px;
    background-color: #c5c940;
}

.contact_res_title h3{
    font-size:16px;
    color:#faf7ed;
    font-weight: lighter;
}

.contact_res_circle_email{
    position: absolute;
    top:225px;
    right:10px;
    animation: circle 40s infinite linear;
    background-color: #fffdf3;
    border-radius: 50%;
}

.contact_res_email_icon{
    position: absolute;
    top:285px;
    right:70px;
    display:flex;
    flex-direction: column;
    align-items: center;
    z-index: 9999;
}

.contact_res_email_icon svg{
    fill:#434b50;
    font-size:25px;
}


.contact_res_text {
    width:80%;
    margin: auto;
}

.contact_res_text h3{
    font-size:17px;
    color:#fffdf3;
    font-family: 'Noto Sans KR', sans-serif;
}

.contact_res_text h1{
    font-size: 38px;
    color:#fffdf3;
    margin:10px 0px 40px 0px ;
}

.contact_res_text_email{
    display:flex;
    flex-direction: column;
    font-family: 'Noto Sans KR', sans-serif;
}

.contact_res_text_email p{
    font-size: 13px;
    color:#fffdf3;
    margin:10px 0px 10px 0px;
}